<template>
    <div class="exchange-record">
        <div class="search" :class="{active:searchType}">
            <div class="search-box">
                <el-input :placeholder="$L('请输入搜索订单')" size="small" v-model="inputValue" class="input-with-select"
                          clearable
                          @keyup.enter.native="$event.target.blur"
                          @change="searchFunc">
                    <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
            </div>
            <div class="bbx-font bbx-icon-close mainFontColor" @click="showSearch(false)"></div>
        </div>
        <div class="search-status">
            <el-button size="small" class="time-search" @click="showTimeFunc">{{ $L('时间筛选') }}</el-button>
            <el-select v-model="messageType" size="small"
                       @change="changeTypeFunc">
                <el-option
                        v-for="item in messageTypelist"
                        :key="item.type"
                        :label="item.name"
                        :value="item.type">
                </el-option>
            </el-select>
        </div>
        <div class="exchange-list" v-loading="loading" v-if="tableData && tableData.hasOwnProperty('list') && tableData.list.length>0">
            <div v-for="(item,index) in tableData.list" :key="index" class="list-wrapper">
                <img :src="item.img" alt class="exchange-img">
                <div class="exchange-detail">
                    <p class="tip">{{item.name}}</p>
                    <p class="tip">{{item.typeName}}</p>
                    <p class="tip">{{item.createTime}}</p>
                </div>
                <div class="exchange-ponits mainFontColor">{{item.points}}{{ $L('积分') }}</div>
            </div>
            <!-- 分页 -->
            <div class="list-pages" v-if="tableData.list !== null && tableData.allNum>pageSize">
                <el-pagination
                        small
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :page-size="pageSize"
                        layout="prev, pager, next"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
        <div class="tips" v-else>
            {{ $L('暂无数据') }}
        </div>
        <transition name="fade">
            <div class="time" v-if="showTime">
                <div class="close" @click="closeTimeFunc"></div>
                <div class="date">
                    <calendar></calendar>
                    <inlineCalendar mode="during" :yearName="$L('年')" :monthNames="[$L('一月'), $L('二月'), $L('三月'), $L('四月'),$L( '五月'), $L('六月'),$L( '七月'), $L('八月'), $L('九月'), $L('十月'),$L( '十一月'), $L('十二月')]"
                                    :weekNames="[$L(' 周一'), $L(' 周二'), $L(' 周三'), $L(' 周四'), $L(' 周五'), $L(' 周六'), $L(' 周日')]"
                                    @change="onChange" :defaultDate="defaultDate"></inlineCalendar>
                    <div class="btns">
                        <el-button @click="closeTimeFunc" size="small">{{ $L('取消') }}</el-button>
                        <el-button size="small" type="primary" class="mainBgColor" @click="gotoTime">{{ $L('确认') }}</el-button>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        name: "exchangeRecord",
        data() {
            return {
                loading:false,
                searchType: false,
                inputValue: "",
                SearchTimeZone: "",
                messageType: "all",
                pageSize: 10,
                currentPage: 1,
                dtCreateStart: "",
                dtCreateEnd: "",
                messageTypelist: [
                    {
                        type: "all",
                        name: this.$L("全部类型")
                    },
                    {
                        type: "prod",
                        name: this.$L("产品")
                    },
                    {
                        type: "gift",
                        name: this.$L("礼品")
                    },
                    {
                        type: "coupon",
                        name: this.$L("优惠券")
                    }
                ],
                tableData: {},
                showTime:false,
                defaultDate:[],
            };
        },
        computed: {
            //用于读取和设置顶部按钮
            mobileTopBtn: {
                get() {
                    return this.$store.state.mobileTopBtn;
                },
                set(val) {
                    this.$store.commit('setMobileTopBtn', val);
                }
            }
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 执行设置顶部按钮
            this.setupTopBtn();
            this.showSearch(false);
            next();
        },
        created() {
            //创建当前页面时执行设置顶部按钮
            this.setupTopBtn();
            this.getData();
        },
        beforeDestroy() {
            //销毁当前页面时，必须清空顶部按钮，不然会影响其他页面
            this.mobileTopBtn = null
        },
        methods : {
            setupTopBtn() {
                this.mobileTopBtn = [
                    {
                        icon: 'bbx-icon-sousuo',
                        size: '24px',//图标大小，可以不设置，默认24px
                        func: () => {
                            this.showSearch(true);
                        }
                    }
                ];
            },
            showSearch(type) {
                if (type) {
                    //显示搜索框内容
                    this.searchType = true;
                } else {
                    //隐藏搜索框内容
                    this.searchType = false
                }
            },
            showError(title, tips, type) {
                if (type === "warning") {
                    this.errorType = "warning";
                } else {
                    this.errorType = "error";
                }
                if (title && title !== "") {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L("出错了");
                }
                if (tips && tips !== "") {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            showTimeFunc(){
                this.showTime = !this.showTime;
            },

            closeTimeFunc(){
                this.showTime = false;
                this.defaultDate = [];
                this.dtCreateStart = '';
                this.dtCreateEnd = '';
                this.getData();
            },
            onChange(date) {
                let arr = JSON.stringify(date.map((item) => item.format('YYYY-MM-DD')));
                arr = arr.replace(/"|\[|\]/g,'');
                arr = arr.split(',');
                this.defaultDate = arr;
                this.dtCreateStart = arr[0];
                this.dtCreateEnd = arr[1] ? arr[1]+' 23:59:59' : '';
            },
            gotoTime(){
                this.showTime = false;
                this.getData();
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let arr = {
                    p: _self.currentPage,
                    num: _self.pageSize,
                    type: _self.type,
                    keyword: _self.inputValue,
                    dtCreateStart: _self.dtCreateStart,
                    dtCreateEnd: _self.dtCreateEnd
                };
                _self.$api(
                    "getPointsStoreRecordList",
                    arr,
                    function(reData) {
                        if (reData.flag === 1) {
                            _self.tableData = reData.data;
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self
                                .$confirm(_self.$L("请重新登录"), _self.$L("登录超时"), {
                                    confirmButtonText: _self.$L("重新登录"),
                                    cancelButtonText: _self.$L("取消"),
                                    type: "error",
                                    distinguishCancelAndClose: true
                                })
                                .then(() => {
                                    _self.$router.push({
                                        name: "login",
                                        query: { redirect: _self.$route.fullPath }
                                    });
                                })
                                .catch(action => {
                                    _self.$gotoWebHome();
                                });
                        } else {
                            //未知错误
                            _self
                                .$confirm(_self.$L("数据加载失败"), _self.$L("出错了"), {
                                    confirmButtonText: _self.$L("点击重试"),
                                    cancelButtonText: _self.$L("返回首页"),
                                    type: "error",
                                    distinguishCancelAndClose: true
                                })
                                .then(() => {
                                    _self.getData();
                                })
                                .catch(action => {
                                    _self.$router.push({name:'integralShop'});
                                });
                        }
                    },
                    function(error) {
                        //网络请求错误
                        _self
                            .$confirm(_self.$L("网络请求失败"), _self.$L("出错了"), {
                                confirmButtonText: _self.$L("点击重试"),
                                cancelButtonText: _self.$L("返回首页"),
                                type: "error",
                                distinguishCancelAndClose: true
                            })
                            .then(() => {
                                _self.getData();
                            })
                            .catch(action => {
                                _self.$router.push({name:'integralShop'});
                            });
                    }
                );
            },
            SearchTimeFunc(time) {
                // dtCreateStart   dtCreateEnd
                if (time !== null) {
                    this.dtCreateStart = time[0];
                    this.dtCreateEnd = time[1];
                } else {
                    this.dtCreateStart = "";
                    this.dtCreateEnd = "";
                }
                this.getData();
            },
            searchFunc(val) {
                this.inputValue = val;
                this.getData();
            },
            changeTypeFunc(val) {
                this.type = val;
                this.getData();
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.getData();
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getData();
            },
            stateTypeFunc(i) {
                let state;
                switch (i) {
                    case 0:
                        state = {
                            text: "产品"
                        };
                        break;
                    case 1:
                        state = {
                            text: "礼品"
                        };
                        break;
                    case 2:
                        state = {
                            text: "优惠券"
                        };
                        break;
                    default:
                        state = {
                            text: "未知报错",
                            color: "red"
                        };
                        break;
                }
                return state;
            }
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";
    @import "../../../assets/css/common/lib";
    .exchange-record {
        position: absolute;
        width: 100%;
        height: auto;
        top: 0;
        left: 0;
        .search {
            position: relative;
            width: 100%;
            height: 0;
            line-height: 50px;
            text-align: center;
            overflow: hidden;
            @include diy-animation-show(height);
            border-bottom: 1px solid #F2F6FC;

            &.active {
                height: 50px;
                padding: 0 10px;
                background: #fff;
            }

            .bbx-font {
                position: absolute;
                width: 50px;
                height: 50px;
                line-height: 50px;
                text-align: center;
                top: 0;
                right: 0;
                font-size: 20px;
            }
            .search-box{
                width: calc(100% - 40px);
                /deep/ .el-input__inner{
                    padding: 0 10px;
                }
                /deep/ .el-input-group__append{
                    padding: 0 12px;
                    background: #fff;
                    color: #333;
                    font-size: 16px;
                }
            }
        }
        .search-status{
            height: 46px;
            border-bottom: 1px solid #F2F6FC;
            background: #fff;
            /deep/ .el-select{
                margin: 7px 10px;
                width: calc(35% - 20px);
                vertical-align: top;
            }
            .time-search{
                margin: 7px 0 7px 10px;
            }
        }
        .exchange-list {
            background: #fff;
            margin: 10px 0;
            padding: 10px;
            .list-wrapper{
                border-bottom: 1px solid #eee;
                margin-bottom: 10px;
                padding-bottom: 10px;
                .exchange-detail{
                    width: calc(100% - 160px);
                    height: 80px;
                    display: inline-block;
                    padding-left: 10px;
                    vertical-align: top;
                    .tip{
                        height: 20px;
                        line-height: 20px;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        &:nth-child(2){
                            margin-top: 20px;
                        }
                    }
                }
                .exchange-ponits{
                    line-height: 80px;
                    padding-right: 10px;
                    font-size: 14px;
                    font-weight: 700;
                    width: 80px;
                    text-align: right;
                    display: inline-block;
                    vertical-align: top;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                .exchange-img {
                    width: 80px;
                    height: 80px;
                    white-space: nowrap;
                    overflow: hidden;
                    vertical-align: top;
                }
                &:last-child{
                    margin-bottom: 0;
                    border-bottom: 0;
                }
            }
            .list-pages {
                background: #fff;
                text-align: center;
                padding: 20px 0;
            }
        }
        .tips{
            background: #fff;
            text-align: center;
            color: #999;
            height: 50px;
            line-height: 50px;
            margin-top: 10px;
        }
        .time{
            height: 100%;
            position: fixed;
            z-index: 1000;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            background: rgba(0, 0, 0, 0.6);
            .date{
                width: 100%;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                .btns{
                    background-color: #fff;
                    text-align: right;
                    padding: 5px 10px;
                }
            }
            .close{
                width: 100%;
                height: 100%;
                top: 0;
                right: 0;
                left: 0;
                bottom: 0;
            }
        }
        .fade-enter,
        .fade-leave-to{
            opacity: 0;
        }
        .fade-enter-active,
        .fade-leave-active{
            transition: opacity 0.5s
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #ffffff;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }


    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>